import React from 'react';
import Logo from "../../../shared/components/Logo";

import {ReactComponent as Viber} from "../../../images/icon_social/viber.svg";
import {ReactComponent as Telegram} from "../../../images/icon_social/telegram.svg";
import {ReactComponent as Instagram} from "../../../images/icon_social/instagram.svg";

import styles from "./Footer.module.scss"

function Footer(props) {
    return (
        <div className={styles.container}>
            <footer className={styles.footer}>
                <div className={styles.footer}>
                    <div>
                        <Logo/>
                    </div>
                    <div>
                        <ul className={styles.socialList}>
                            <li className={styles.socialListItem}><a
                                href="https://invite.viber.com/?g=7B01ZxG3Tkv174xujmcvt163ggekbtcV"><Viber
                                className={styles.iconViber}/></a>
                            </li>
                            <li className={styles.socialListItem}><a
                                href="https://t.me/joinchat/3xy0R9IDtUVmYjIy"><Telegram
                                className={styles.iconViber}/></a>
                            </li>
                            <li className={styles.socialListItem}><a
                                href="https://t.me/joinchat/3xy0R9IDtUVmYjIy"><Instagram className={styles.iconViber}/></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </footer>
        </div>
    );
}

export default Footer;