import React from 'react';

import styles from "./ListContactItem.module.scss"

function ListContactItem({contact}) {
    const {Logo} = contact
    return (
        <li className={styles.contactListItem}>
            <a href={contact.href} className={styles.contactListItemLink}><Logo className={styles.icon}/>{contact.number}</a>
        </li>
    );
}

export default ListContactItem;